.trailer-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* Mengubah dari 70.25% ke 56.25% untuk rasio 16:9 */
  height: 100%;
  overflow: hidden;
  background-color: transparent; /* Latar belakang transparan */
}

.video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* Menghindari kontrol pada video */
}

.trailer-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent; /* Menghilangkan warna latar belakang hitam */
}

.trailer-title-overlay {
  position: absolute;
  top: 50%; /* Posisikan di tengah vertikal */
  left: 20px; /* Jarak dari kiri */
  transform: translateY(-50%); /* Menggeser setengah tinggi teks ke atas untuk tengah vertikal */
  color: white; /* Warna teks */
  font-size: 50px; /* Ukuran font */
  background: transparent;
  padding: 10px; /* Padding untuk keterbacaan */
  border-radius: 5px; /* Sudut membulat */
  pointer-events: none; /* Nonaktifkan interaksi */
  font-weight: bold; /* Membuat teks menjadi bold */
}

.trailer-description {
  font-size: 16px; /* Ukuran font deskripsi */
  margin-top: 5px; /* Jarak antara judul dan deskripsi */
  max-width: 280px; /* Maksimal lebar deskripsi */
  display: -webkit-box; /* Menggunakan flexbox untuk multiline */
  -webkit-box-orient: vertical; /* Mengatur orientasi kotak menjadi vertikal */
  -webkit-line-clamp: 3; /* Membatasi menjadi 2 baris */
  overflow: hidden; /* Menyembunyikan teks yang melebihi batas */
  text-overflow: ellipsis; /* Menambahkan ellipsis (...) untuk teks yang terpotong */
  color: white; /* Warna teks */
  font-weight: normal;
}

.movie-list {
  overflow-x: auto; /* Mengizinkan scroll horizontal */
  white-space: nowrap; /* Mencegah elemen dipecah menjadi baris baru */
  padding: 10px 0; /* Padding vertikal untuk estetika */
}

.movie-scroll {
  display: inline-flex; /* Menggunakan flexbox untuk item film */
}

.movie-item {
  display: inline-block; /* Pastikan item film ditampilkan dalam satu baris */
  margin-right: 10px; /* Jarak antara item film */
}

.movie-poster-container {
  text-align: center; /* Memusatkan gambar di dalam kontainer */
}

.movie-poster {
  width: 150px; /* Sesuaikan lebar gambar sesuai keinginan */
  height: auto; /* Menjaga aspek rasio gambar */
  border-radius: 5px; /* Membulatkan sudut gambar (opsional) */
  transition: transform 0.3s; /* Animasi saat hover (opsional) */
}

.movie-item:hover .movie-poster {
  transform: scale(1.1); /* Membesarkan gambar saat dihover */
}

/* Hapus atau kosongkan kelas ini untuk menghilangkan judul film */
.movie-title {
  display: none; /* Tidak menampilkan judul film */
}
